import styled from 'styled-components'

const Container = styled.div`
  position: relative;

  input {
    opacity: 0;

    position: absolute;
    top: 13px;
    left: 13px;
    transform: translate(-50%, -50%);
  }

  label {
    display: grid;
    gap: 12px;
    grid-template-columns: auto 1fr;
    align-items: center;

    cursor: pointer;

    &::before {
      display: block;
      content: '';

      width: 24px;
      height: 24px;
      border: 1px solid #cdccca;
      border-radius: 50%;

      background-image: url('data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIxMiIgaGVpZ2h0PSIxMiIgdmlld0JveD0iMCAwIDEyIDEyIiBmaWxsPSJub25lIj4KPGNpcmNsZSBjeD0iNiIgY3k9IjYiIHI9IjUiIGZpbGw9IndoaXRlIi8+Cjwvc3ZnPg==');
      background-position: center;
      background-repeat: no-repeat;
    }

    &:hover {
      &::before {
        border-color: #6f6f69;
      }
    }
  }

  &:has(input:checked) {
    label {
      &::before {
        background-color: #0042ff;
        border-color: #0042ff;
      }
    }
  }
`

type RadioProps = {
  id: string
  name: string
  label: string
  required?: boolean
  checked: boolean
  onChange: (event: React.ChangeEvent<HTMLInputElement>) => void
}

export const Radio = ({ id, name, label, required = false, checked, onChange }: RadioProps) => (
  <Container>
    <input
      id={id}
      type="radio"
      name={name}
      value={id}
      required={required}
      checked={checked}
      onChange={onChange}
    />
    <label htmlFor={id}>{label}</label>
  </Container>
)
